.introduce {
  width: 781px;
  height: 269px;
}
.introduce .title {
  width: 137px;
  height: 37px;
  line-height: 37px;
  text-align: center;
  font-size: 16px;
  letter-spacing: 1px;
  color: #ffffff;
}
.introduce .kuang {
  margin-top: 10px;
  width: 781px;
  height: 222px;
  overflow: hidden;
}
.introduce .kuang .beijing {
  position: relative;
  margin: 14px 0 0 13px;
  width: 812px;
  height: 200px;
