}
.introduce .kuang .beijing .left {
  float: left;
  display: inline-block;
  width: 346px;
  height: 200px;
}
.introduce .kuang .beijing .left .left_top {
  margin: 15px 0 0 14px;
  width: 346px;
  height: 72px;
}
.introduce .kuang .beijing .left .left_buttom {
  margin: 11px 0 0 14px;
  width: 346px;
  height: 95px;
  font-size: 14px;
  color: #ffffff;
}
.introduce .kuang .beijing .right {
  margin: 32px 0 0 25px;
  float: left;
  display: inline-block;
  width: 378px;
  height: 151px;
}
.introduce .kuang .beijing h3 {
  position: absolute;
  left: 630px;
  top: 12px;
  font-size: 14px;
  letter-spacing: 1px;
  color: #00ffff;
